.i {
    display: flex;
    height: 100vh;
}

.i-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.i-right {
    flex: 1;
    position: relative;
}

.i-left-wrapper {
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly

}

.i-intro {
        font-size: 30px;
        font-weight: 300;
}


.i-name {
    font-size: 60px;
}

.i-title {
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper {
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
    20%{
        transform: translateY(-50px);
    }
    40%{            
        transform: translateY(-100px);
    }
    60%{            
        transform: translateY(-150px);
    }
    80%{            
        transform: translateY(-200px);
    }
    100%{            
        transform: translateY(-250px);
    }

}

.i-title-item {
    height: 50px;
    width: 300px;
    font-size: 24px;
    font-style: 30px;
    font-weight: bold;
    color: lightgreen;
    display: flex;
    align-items: center;
}

.i-scroll {
    width: 100px;
    height: 100px;
    position: absolute;
    bottom: 20px;
}

.i-img {
    width: 100%;
    height: 100%;
    object-fit: scale-down;
    position: absolute;
}

.i-bg {
    clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
    background-color: lightgreen;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    right: 0;
}

.i-desc {
    font-size: 25px;
}

@media screen and (max-width: 480px){
    .i {
        flex-direction: column;
    }

    .i-left-wrapper {
        padding: 10px;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 100%;
    }

    .i-desc {
        display: none;
    }
}